<template>
  <el-container>
    <el-header class="head-container" style="height:140px">
      <button id="back" @click="goBack">
        <img style="width:17px;height:31px" src="../assets/courseware/back_icon.png">
        <p>返回</p>
        </button>
        <div class="head-user" v-if="bankType==2">
          <button id="username">
            <img style="width:64px;height:64px" src="../assets/courseware/headshot.png">
            <p>用户名称</p>
          </button>
        </div>
        <div>
          <button id="login-out" v-if="bankType==2">
            <img style="width:24px;height:24px" src="../assets/courseware/out_icon.png">
            <p>退出登录</p>
          </button>
        </div>
    </el-header>
    <el-container>
      <el-aside>
        <el-menu default-active="0" active-text-color="#ffffff" background-color="#17A1FF">
          <el-menu-item index="1" class="full-btn" style="height:80px;line-height:80px">全部课件</el-menu-item>
          </el-menu>
      </el-aside>
      <el-main>
        <el-scrollbar id="main" class="app-main" ref="myScrollbar" style="height: 85vh;">
          <el-row>
            <el-col :span="4" v-for="item in tableData"  :key="item.id" :img_url="item.cover_img_url" :offset="0">
              <div class="card-container" @click="goDetail(item.id)">
                <el-image style="height: 7.8vw; border-radius:4px" :src="item.cover_img_url" :fit="fit">
                </el-image>
                <div style="margin:19px 0px 29px 0px">
                  <span class="activity_title">{{item.activity_title}}</span>
                </div>
              </div>
            </el-col>
          </el-row>
        </el-scrollbar>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import { getList } from '../service/courseware'
export default {
    data() {
        return {
            bankType: 1,
            loading: false,
            page: 1,
            tableData: []
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        async init() {
            const { bankType } = this.$route.query
            this.bankType = bankType
            const { data, } = await getList({
              pageNumber: this.page,
              type: this.bankType
            })
            this.tableData = this.tableData.concat(data)
        },
        goBack() {
            this.$router.go(-1)
        },
        goDetail(id) {
            this.$router.push( { path: "/broadcast", query: { bankType: this.bankType, planId: id} } )
        },

        getList() {
            this.init()
        },
        onLoad() {
            this.init()
        },
        onRefresh() {
            this.page = 1
            this.loading = true
            this.onLoad()
        },
    }
}
</script>

