
<template>
    <div>
        <iframe class="webview" v-bind:src="url" scrolling="no" frameborder="0"></iframe>
    </div>
</template>

<script>
export default {
    data() {
        return {
            planId: null,
            bankType: null,
            url: ""
        }
    },
    mounted() {
      const { bankType, planId } = this.$route.query;
      this.planId = planId;
      this.bankType = bankType;
      this.url = `https://m.mdedutech.com/lesson/lessonPreview.html?planId=${planId}&type=${bankType}`
    },
}
</script>
<style>
.webview {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
</style>